@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
:root {
  --textColor: rgb(244,59,71);
  --textDefaultColor: rgba(0, 0, 0, 0.8);
  --listDivider: rgba(0, 0, 0, 0.3);
  --headerTextColor: rgb(157,32,83);
  --headerBackground: rgba(52, 25, 49, 0.7);
  --backgroundGradient: linear-gradient(to top, #f43b47 0%, #453a94 100%);
}

body {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  /*
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

label {
  color: rgb(244,59,71);
  color: var(--textColor);
  margin-right: 12px;
  font-size: 25px;
  line-height: 46px;
}

html {
  color: rgba(0, 0, 0, 0.8);
  color: var(--textDefaultColor);
  background-image: -webkit-gradient(linear, left bottom, left top, from(#f43b47), to(#453a94));
  background-image: -webkit-linear-gradient(bottom, #f43b47 0%, #453a94 100%);
  background-image: linear-gradient(to top, #f43b47 0%, #453a94 100%);
  background-image: var(--backgroundGradient);
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

input {
  border: 1px solid rgb(157,32,83);
  border: 1px solid var(--headerTextColor);
  background: rgba(0,0,0,0.4);
  padding: 4px;
  font-size: 25px;
  margin: -4px 0;
  color: rgb(244,59,71);
  color: var(--textColor);
  box-sizing: border-box;
}

button {
  padding: 8px 30px;
  font-size: 18px;
  border: 1px solid #f43a47;
  color: #f43a47;
  background: rgb(157,32,83);
  background: var(--headerTextColor);
}

button:disabled {
  color: graytext;
  opacity: 0.4;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*:focus {
  outline: none;
}

::-webkit-input-placeholder {
  color: rgb(244,59,71);
  color: var(--textColor);
  opacity: 0.4;
}

::-moz-placeholder {
  color: rgb(244,59,71);
  color: var(--textColor);
  opacity: 0.4;
}

:-ms-input-placeholder {
  color: rgb(244,59,71);
  color: var(--textColor);
  opacity: 0.4;
}

::-ms-input-placeholder {
  color: rgb(244,59,71);
  color: var(--textColor);
  opacity: 0.4;
}

::placeholder {
  color: rgb(244,59,71);
  color: var(--textColor);
  opacity: 0.4;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
