    padding: 16px;
    height: 60px;
    box-sizing: border-box;
    transition: height 0.4s;
    -webkit-transition: height 0.4s;
}

footer.expanded {
    height: 300px;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    overflow: hidden;
    -webkit-animation: borderRadius 0.5s;
            animation: borderRadius 0.5s;
}

.footer-menu {
    opacity: 0;
}

footer.expanded .footer-menu {
    opacity: 1;
}

footer span {
    text-overflow: ellipsis;
